import React, { useEffect, useState } from 'react';
import { styled } from '@mui/material/styles';

import { Grid, Table, TableBody, TableCell, Stack, tableCellClasses, Card, IconButton, Menu, MenuItem, CardContent, Button, TextField, TableContainer, TableHead, TableRow, Tooltip, Fab } from '@mui/material';
// project imports
import { useDispatch, useSelector } from 'store';
import { dispatch } from 'store';

const StyledTableCell = styled(TableCell)(({ theme }) => ({
    [`&.${tableCellClasses.head}`]: {
        maxWidth: 50
    }
}));
export default function Review() {
    const inventorydataState = useSelector((state) => state.inventory);

    const [AllDataList, setAllDataList] = useState([]);
    useEffect(() => {
        const filterdata = inventorydataState.inventorydata.filter((item) => {
            return Object.values(item).some((value) => value !== "");
        })
        setAllDataList(filterdata);
    }, [inventorydataState]);
    return (
        <>
            <Grid container spacing={3}>
                <Grid item xs={12}>
                    <TableContainer>
                        <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
                            <TableHead>
                                <TableRow>
                                    <TableCell align='center' colSpan={8}></TableCell>
                                    <TableCell align='center' colSpan={3}>Total Area</TableCell>
                                    <TableCell align='center' colSpan={5}></TableCell>

                                </TableRow>
                                <TableRow>
                                    <TableCell align='center'>Balance</TableCell>
                                    <TableCell align='center'>Debit</TableCell>
                                    <TableCell align='center'>Credit</TableCell>
                                    <TableCell align='center'>Tips</TableCell>
                                    <TableCell align='center'>Cutting  Part Of Block</TableCell>
                                    <TableCell align='center'>Sawig Post</TableCell>
                                    <TableCell align='center'>Unit Cost</TableCell>
                                    <TableCell align='center'>Cubic Meter</TableCell>
                                    <TableCell align='center'>Height</TableCell>
                                    <TableCell align='center'>Width</TableCell>
                                    <TableCell align='center'>Lengh</TableCell>
                                    <TableCell align='center'>Block number</TableCell>
                                    <TableCell align='center'>Material Name</TableCell>
                                    <TableCell align='center'>Sawing</TableCell>
                                    <TableCell align='center'>No</TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {AllDataList.map((row) => (
                                    <TableRow hover key={row.id}>
                                        {Object.keys(row).map((item, i) => (
                                            <>
                                                {item != "id" && (
                                                    <TableCell align="center" key={i}>{row[item]}</TableCell>
                                                )}
                                            </>
                                        ))}
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    </TableContainer>
                </Grid>
            </Grid>
        </>
    );
}
